.gameContainer {
  max-width: 1000px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 10px;
  background-color: #fef8e8;
  padding: 20px;
  border-radius: 10px;
}

.madeMoves {
  margin: 10px 0;
  font-size: 20px;
}

.card {
  max-width: 150px;
  cursor: pointer;
}

.card.flipped>div {
  transform: rotateY(180deg);
}

.resetButton {
  margin: 20px 0;
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  font-size: 22px;
  background-color: #a5dae9;
  color: #4d4d4d;
  transition: 0.2s;
}

.resetButton:hover {
  background-color: #875085;
}

.resetButton:active {
  background-color: #734471;
}

@media only screen and (max-width: 425px) {
  .gameContainer {
    grid-template-columns: repeat(3, 1fr);
    padding: 7px;
    gap: 7px;
    border-radius: 15px;
  }
  .resetButton{
    margin: 5px 0 10px 0;
    padding: 5px;
    font-size: 20px;
    width: 70%;
  }
  .madeMoves{
    margin: 0 0 5px 0;
    font-size: 18px;
  }
}

@media only screen and (max-height: 750px) {
  .card {
    max-width: 130px;
  }
}

@media only screen and (max-height: 600px),and (min-width:768px) {
  .gameContainer {
    grid-template-columns: repeat(6, 1fr);
  }
}
